import React from 'react';
import s from '../Profile.module.css';

let ProfileData = ({profile, isOwner, goToEditMode}) => {
    return (
        <div>
            {isOwner && <div><button onClick={goToEditMode}>edit</button></div>}
            <p>data</p>
            {Object.keys(profile.contacts).map((p)=>
                <div className={s.soshialwrap}>
                    <p>{p} - </p>
                    <a href={profile.contacts[p]}>{profile.contacts[p] ? profile.contacts[p] : ' '}</a>
                </div>
            )}
            <div><span>  - </span>{profile.aboutMe && <span>{profile.aboutMe}</span>}</div>
            {profile.lookingForAJob === true ? <p>  </p> : <p>  </p>}
            <div><span>  - </span>
            {profile.lookingForAJobDescription
                ? <span>{profile.lookingForAJobDescription}</span>
                : <span> </span>}</div>
            <div><span>  - </span>
            {profile.fullName != null
                ? <span>{profile.fullName}</span>
                : <span> </span>}</div>
        </div>
    );
};

export default ProfileData;